/* global console, Blob, fetch */

import * as msal from "@azure/msal-browser";
import * as MicrosoftGraph from "@microsoft/microsoft-graph-client";

import { sampleDict } from "./sample";

const msalConfig = {
  auth: {
    clientId: "b8f7e758-dbc0-404c-886b-72f7fb9a3414", //   ID  
    authority: "https://login.microsoftonline.com/common",
    redirectUri: "https://localhost:3000",
    loginPersistence: true, //   
  },
  system: {
    loggerOptions: {
      loggerCallback: (level, message) => {
        console.log(`MSAL Log: ${message}`);
      },
      piiLoggingEnabled: false,
    },
  },
  cache: {
    cacheLocation: "localStorage", //    
    storeAuthStateInCookie: true, //    
  },
};

const msalInstance = new msal.PublicClientApplication(msalConfig);

// MSAL   
async function initializeMsal() {
  try {
    // MSAL v3 initialize()  
    await msalInstance.initialize();
  } catch (error) {
    console.error("MSAL initialization error:", error);
    throw error;
  }
}

//   
const loginRequest = {
  scopes: ["Files.ReadWrite", "User.Read"],
};

async function signIn() {
  try {
    // MSAL    
    await initializeMsal();

    //  
    const response = await msalInstance.loginPopup(loginRequest);
    msalInstance.setActiveAccount(response.account);
    return response.account;
  } catch (error) {
    console.error("Login failed:", error);
    throw error;
  }
}

async function getGraphClient() {
  // MSAL  
  await initializeMsal();

  //   
  const account = msalInstance.getActiveAccount();
  if (!account) {
    throw new Error("No active account");
  }

  const request = {
    scopes: loginRequest.scopes,
    account: account,
  };

  const response = await msalInstance.acquireTokenSilent(request);

  // Microsoft Graph  
  return MicrosoftGraph.Client.init({
    authProvider: (done) => {
      done(null, response.accessToken);
    },
  });
}

async function createFolder(folderName = "myapp") {
  await initializeMsal();
  const client = await getGraphClient();

  try {
    //    
    try {
      await client.api(`/me/drive/root:/${folderName}`).get();
      console.log(`Folder '${folderName}' already exists`);
      return true; //   
    } catch (error) {
      //   
      if (error.statusCode === 404) {
        await client.api("/me/drive/root/children").post({
          name: folderName,
          folder: {},
        });
        console.log(`Folder '${folderName}' created successfully`);
        return true;
      } else {
        throw error;
      }
    }
  } catch (error) {
    console.error("Error creating folder:", error);
    return false;
  }
}

